











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class HelloWorld extends Vue {
  @Prop() private value: string = ''
  @Prop() private placeholder: string = '请输入'
  @Prop() private clear: Boolean = true
  @Prop() private disabled: Boolean = true
  // @Prop() private rules!: Array
  val = this.value
  ifFocus = []
  ifBlur = []
  ifChange = []
  isFocus = false
  isError = false
  public created():void {
    // for(let k = 0; k < this.rules.length; k ++) {
    //   if(this.rules[k].trigger == 'focus') {
    //     this.ifFocus = [true, this.rules[k].message]
    //   }
    //   else if(this.rules[k].trigger == 'blur') {
    //     this.ifBlur = [true, this.rules[k].message]
    //   }
    //   else if(this.rules[k].trigger == 'change') {
    //     this.ifChange = [true, this.rules[k].message]
    //   }
    // }
  }
  public focus():void {
    this.isFocus = true
  }
  public blur() {
    this.isFocus = false
    if(this.ifBlur[0]) {
      if(!this.val) {
        this.isError = true
        alert(this.ifBlur[1])
      } else {
        this.isError = false
      }
    } 
  }
  public vaildent() {
    if(this.ifChange) {
      let r = /^([a-zA-Z]|[0-9])+@+(qq|163|sina)+.com$/;
      if (!r.test(this.val)) {
        alert('不是邮箱')
      }
    }
  }
  public getClass() {
    if(this.isFocus) return 'borderChecked'
    if(this.isError) return 'error'
  }
  public clearVal() {
    this.val = ''
  }
}
